@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam:wght@400;500;700&display=swap);
.Header{height:80px;position:fixed;top:0;left:0;right:0;padding:0 30px;z-index:100;background:white}@media (min-width: 950px){.Header{padding:0 60px}}.Header button:hover{opacity:.8}.hamburger{cursor:pointer}@media (min-width: 950px){.hamburger{display:none}}.mobileNav{display:flex;justify-content:center;align-items:center;margin:0 30px;flex-flow:column;border-radius:5px;padding:40px 0;opacity:0;background:white;position:fixed;top:0;right:0;left:0;z-index:1000;margin-top:80px;box-shadow:2px 6px 20px 3px rgba(0,0,0,0.1),0 0 0 100vh rgba(0,0,0,0.1)}.mobileNav li{padding:10px 0;font-weight:bold;color:#242d52;font-size:16px;cursor:pointer;opacity:0;margin-left:20px;cursor:poiner}.mobileHeader{display:flex;justify-content:space-between;align-items:center;height:100%}@media (min-width: 950px){.mobileHeader{display:none}}.desktopNav{display:none;width:100%;justify-content:space-between;align-items:center;height:100%}.desktopNav li{padding:0 10px;cursor:pointer}@media (min-width: 950px){.desktopNav{display:flex}}li:hover{color:#f25f3a}

.Button{background:#f25f3a;color:white;cursor:pointer;border:none;border-radius:50px;padding:15px 25px;font-weight:bold;box-shadow:0 13px 10px -9px rgba(0,0,0,0.2);font-size:14px}

.Hero{padding:60px 30px;padding-top:calc(60px * 2);text-align:center;position:relative;overflow:hidden}.Hero p{padding:30px 40px}@media (min-width: 950px){.Hero p{padding:30px 0}}@media (min-width: 950px){.Hero{display:flex;margin-top:60px;padding:60px 60px}}.Hero__background{position:absolute;top:-120px;right:-120px;z-index:-1}@media (min-width: 950px){.Hero__background{top:-300px;right:-300px}}.Hero__backgroundBottom{position:absolute;bottom:20px;right:-70%;z-index:-1}@media (min-width: 950px){.Hero__backgroundBottom{display:none}}.Hero__textSide{display:flex;flex-flow:column;justify-content:center;align-items:center}@media (min-width: 950px){.Hero__textSide{order:-1;flex:1 1;text-align:left;align-items:flex-start}}@media (min-width: 950px){.Hero__imgSide{flex:1 1}}

.WhatsDifferent{padding:0 30px;text-align:center;position:relative}.WhatsDifferent p{padding:30px}@media (min-width: 950px){.WhatsDifferent{display:flex;padding:0 60px}}.reason strong{display:flex;align-items:center;justify-content:flex-start;width:100%;position:relative}.reason strong::after{content:'';background:#f25f3a;position:absolute;top:0;bottom:0;opacity:.1;right:0;left:10px;transform:translateX(40px)}@media (min-width: 950px){.reason strong::after{display:none}}.reasonOne strong::before{content:'01';background:#f25f3a;color:white;padding:8px 20px;display:flex;justify-content:center;align-items:center;border-radius:30px;margin-right:30px}.reasonTwo strong::before{content:'02';background:#f25f3a;color:white;padding:8px 20px;display:flex;justify-content:center;align-items:center;border-radius:30px;margin-right:30px}.reasonThree strong::before{content:'03';background:#f25f3a;color:white;padding:8px 20px;display:flex;justify-content:center;align-items:center;border-radius:30px;margin-right:30px}@media (min-width: 950px){.WhatsDifferent__leftSide{text-align:left}.WhatsDifferent__leftSide p{padding-left:0}}@media (min-width: 950px){.WhatsDifferent__rightSide{text-align:left;padding-left:40px}}

.Testimonials{padding:100px 30px 150px 30px;text-align:center}@media (min-width: 950px){.Testimonials{padding:100px 0 150px}}.testimonialSlide{background:#fafafa;padding:30px 30px;position:relative;padding-top:60px}.testimonialSlide img{position:absolute;top:0;left:50%;transform:translate(-50%, -50%);z-index:3;height:100px;width:100px}.testimonialSlideContainer{padding:100px 20px 0 20px}

.Simplify{background:#f25f3a;color:white;padding:60px 30px;text-align:center;position:relative;overflow:hidden}.Simplify button{padding:15px 25px;background:white;border:none;border-radius:20px;color:#f25f3a;font-weight:bold;margin:30px;box-shadow:0 13px 10px -9px rgba(0,0,0,0.2)}.Simplify img{position:absolute}.Simplify__mobileImage{left:0;bottom:0}@media (min-width: 930px){.Simplify__mobileImage{display:none}}.Simplify__desktopImage{display:none}@media (min-width: 930px){.Simplify__desktopImage{display:inline;display:initial}}@media (min-width: 950px){.Simplify{display:flex;justify-content:space-between;align-items:center;padding:60px 60px}.Simplify h1{width:45%;font-size:34px;text-align:left;line-height:1.2}}

.Footer{background:#1d1e25;color:white;padding:60px 30px;display:flex;flex-flow:column;align-items:center}@media (min-width: 950px){.Footer{flex-flow:row;align-items:flex-start}}.Footer form{display:flex;width:100%}.Footer form input{border:none;padding:15px;border-radius:40px;width:100%;margin-right:10px;padding-left:30px}@media (min-width: 950px){.Footer form{order:2;flex:1 1}}.Footer__logoAndSocial{display:flex;flex-flow:column;align-items:center}@media (min-width: 950px){.Footer__logoAndSocial{order:-1;flex:1 1}}.footerNav{padding:30px;width:100%;padding-top:0}.footerNav ul{height:170px;display:flex;flex-flow:column wrap;align-content:space-around}.footerNav ul li{margin:5px 0}.footerNav ul li:hover{color:#f25f3a;cursor:pointer}@media (min-width: 950px){.footerNav{flex:1 1}}.social{width:100%;display:flex}@media (min-width: 950px){.social{justify-content:center}}.social svg{margin:40px 10px}.social svg:hover{color:#f25f3a;cursor:pointer}

html{box-sizing:border-box;font-size:16px}*,*::before,*::after{box-sizing:inherit;margin:0;padding:0}ol,ul{list-style:none}img{max-width:100%;height:auto}body{font-family:"Be Vietnam";font-size:16px;color:#242d52;line-height:1.8}h1{font-size:44px;line-height:1.5}p{opacity:.6;font-size:17px}

